.navbar {
  background-color: #121212;
  padding: 10px 20px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  width: 100%;
  top: 0;
  left: 0;
  z-index: 1000;
}

/* Ensure nav-links are aligned to the right */
.nav-links {
  list-style: none;
  display: flex;
  gap: 1.6rem;
  margin-left: auto; /* Push links to the right */
  z-index: 1000;
  margin-right: 2%;
}

/* Navigation link styles */
.nav-links a {
  text-decoration: none;
  color: #fff;
  font-weight: bolder;
  font-size: 20px;
}

.nav-links a:hover {
  color: #3761d5c6;
}

/* LOGO */
.logo {
  display: flex;
  align-items: center;
  justify-content: center;
  position: relative;
  font-family: "Arial", sans-serif;
  height: 85px; /* Reduced overall size */
  width: fit-content;
  margin-left: 20px; /* Centering the logo */
}

/* U & A Container */
.u-a-container {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* Styling for U and A */
.u-part,
.a-part {
  font-size: 3.8rem; /* Reduced font size */
  font-weight: bold;
  color: white;
  position: relative;
  z-index: 2; /* Ensure letters are above the line */
}

.a-part {
  color: #9b59b6;
}

/* The cut line effect */
.line {
  position: absolute;
  width: 120px; /* Increased width for better balance */
  height: 9px; /* Increased thickness */
  background: black;
  top: 55%;
  left: 50%;
  transform: translateX(-50%);
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 5px;
  z-index: 3;
}

/* Ensuring text fits perfectly within the line */
.line span {
  font-size: 0.7rem;
  background-color: transparent;
  position: relative;
  z-index: 4;
  white-space: nowrap;
}

/* Align text properly inside */
.designer {
  color: #9b59b6;
  font-size: 0.3rem;
  margin-right: 3px; /* Spacing between Designer & Developer */
}

.developer {
  color: white;
  font-size: 0.3rem;
}

/* Ensuring the cut effect through UA */
.u-part::before,
.a-part::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 0;
  width: 100%;
  height: 8.2px; /* Match the cut line */
  background: black;
  z-index: 1;
}

/* Resume Badge */
.resume-badge {
  color: #ff00ff;
  font-weight: bold;
  font-size: 15px;
  text-decoration: none;
  cursor: pointer;
  text-align: center;
}

/* -------------------- Responsive Design -------------------- */

/* Hamburger Menu */
.menu-icon {
  display: none;
  cursor: pointer;
}

.menu-icon .bar {
  width: 40px;
  height: 5px;
  background: white;
  margin: 5px 0;
  transition: 0.3s;
}

.open .bar:nth-child(1) {
  transform: rotate(45deg) translate(5px, 5px);
}

.open .bar:nth-child(2) {
  opacity: 0;
}

.open .bar:nth-child(3) {
  transform: rotate(-45deg) translate(5px, -5px);
}

/* Responsive Styles */
@media screen and (max-width: 768px) {
  .nav-links {
    position: absolute;
    top: 50px;
    right: 1%;
    width: 100%;
    background: #444343;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 0;
    overflow: hidden;
    transition: height 0.4s ease-in-out;
  }

  .nav-links.open {
    height: 285px;
    padding: 20px 0;
  }

  .nav-links li {
    display: block;
    width: 100%;
    text-align: center;
  }

  .nav-item, .resume-badge {
    display: block;
    padding: 12px 0;
    width: 100%;
  }

  .menu-icon {
    display: block;
  }
}
