.projects-page {
  position: relative;
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}

.projects-canvas {
  position: absolute !important;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.controls-vertical-left {
  position: absolute;
  top: 60%;           /* vertically center */
  left: 20px;         /* adjust how far from the left edge */
  transform: translateY(-50%); /* so it's truly centered vertically */
  display: flex;
  flex-direction: column; /* stack buttons vertically */
  gap: 10px;         /* spacing between buttons */
  z-index: 9999;     /* ensure they're above the canvas if you want them visible 
                        or lower if you truly want them behind the 3D scene */
}

.controls-vertical-left button {
  padding: 10px 18px;
  background: #3ee5eae7;
  color: #fff;
  border: none;
  cursor: pointer;
  font-size: 0.9rem;
}

.controls-vertical-left button:hover {
  background: #f61df2;
}


/* 📌 Responsive Adjustments for Tablets */
@media screen and (max-width: 1024px) {
  .projects-page {
    height: auto;
    min-height: 120vh;
    padding-bottom: 30px;
  }

  .projects-canvas {
    height: auto;
    min-height: 100vh;
  }

  .controls-vertical-left {
    left: 10px; /* Reduce left padding for smaller screens */
    top: 50%;
    gap: 8px;
  }

  .controls-vertical-left button {
    padding: 8px 14px;
    font-size: 0.85rem;
  }
}

/* 📌 Responsive Adjustments for Mobile Devices */
@media screen and (max-width: 768px) {
  .controls-vertical-left {
    left: 5px; /* Move closer to the left for smaller screens */
    top: 50%;
    gap: 6px;
  }

  .controls-vertical-left button {
    padding: 6px 12px;
    font-size: 0.8rem;
  }
}

/* 📌 Extra Small Screens */
@media screen and (max-width: 480px) {
  .controls-vertical-left {
    left: 2px; /* Minimize padding on the smallest screens */
    top: 55%; /* Adjust to better align */
    gap: 5px;
  }

  .controls-vertical-left button {
    padding: 5px 10px;
    font-size: 0.75rem;
  }
}
